.prodJumbo{
    h2, p, ul{
        color: white;
        letter-spacing: 1.2px;
    }
    h2 {
        font-size: 550%;
    }
    p, li{
        font-size: 150%;
    }
    #iMonitoring{
        font-size: 1650%;
        color:white;
        margin-left:45%
    }
}
.subscribeBtn{
    width:50%;
    background: #FFA500;
    color: white;
    border: white;
    border:3px solid white;
    border-radius: 10px;
    padding: 20px;
    font-size: 150%;
    &:hover{
        background: white;
        color: #FFA500;
        cursor: pointer;
    }
}
.productsGridLayout{
    display: flex;
    flex-wrap: wrap;
    background-color: white;
}
