.productsGridLayout > div{
    background-color: #f1f1f1;
    box-shadow: 5px 5px 5px rgb(175, 175, 175);
    width: 400px;
    margin: 10px;
    text-align: center;
    line-height: 75px;
}
.divImg img{
    width:400px;
    height: auto;
}

 @media screen and (min-width: 992px) {
.productsGridLayout > div{
    background-color: #f1f1f1;
    box-shadow: 5px 5px 5px rgb(175, 175, 175);
    width: 300px;
    margin: 10px;
    text-align: center;
    line-height: 75px;
}
.divImg img{
    width:300px;
    height: auto;
}
} 

.prodStats{
    background: #333333;
    color: white;
    padding: 10px;
    li{
        padding:10px;
        letter-spacing: 1.2px;
    }
}
.magnifying-glass {
    border:#333333; 
    box-shadow: 3px 3px 3px rgb(105, 105, 105);
  }