.jumboLogo{
font-size: 1500%;
color: white;
padding-left: 50px;
h1{
    color: white;
    font-size:150%;
    font-weight: 450;
    letter-spacing: 1.2px;
    padding-top:50px; 
}
}
.startNowBtn{
    width: 100%;
    background: #20B2AA;
    color: white;
    border:3px solid white;
    border-radius: 10px;
    padding: 20px;
    font-size: 180%;
    &:hover{
        background: white;
        color: #20B2AA;
        cursor: pointer;
    }
}
.jumboList{
color: white;
font-size:150%;
font-weight: 450;
letter-spacing: 1.2px;
padding-top:50px; 
    li{
        padding: 25px
    }
}
.jumboText{
    color: white;
    font-size:150%;
    font-weight: 450;
    letter-spacing: 1.2px;
    padding-top:50px; 
    }
.lastButtons{
    width:100%;
    background: #8A2BE2;
    color: white;
    border: white;
    border:3px solid white;
    border-radius: 10px;
    padding: 20px;
    font-size: 150%;
    &:hover{
        background: white;
        color: #8A2BE2;
        cursor: pointer;
    }
}
.prodTable{
    border:4px solid darken($color: #8A2BE2, $amount: 20%);
    width:100%;
    border-radius: 25px;
    tr,td,th{
        color:white;
    }
    th{
        font-size:150%;
        padding: 10px 0 10px 0;
        background: darken($color: #8A2BE2, $amount: 20%)
    }
    td,tr{
        font-size:130%;
    }
    td{
        padding: 10px;
    }
    .trHover{
        &:hover{
            background: lighten($color: #8A2BE2, $amount: 20%)
        }
    }
    .trDownload{
        background: darken($color: #8A2BE2, $amount: 20%);
        .freeBtn{
            width:75%;
            background: lighten($color: #8A2BE2, $amount: 30%);
            color: white;
            border:none;
            border-radius: 10px;
            padding: 5px;
            font-size: 110%;
            &:hover{
                background: lighten($color: #8A2BE2, $amount: 50%);
                color: lighten($color: #8A2BE2, $amount: 20%);
                cursor: pointer;
            }
        }
        .advBtn{
            width:75%;
            background: lighten($color: #8A2BE2, $amount: 20%);
            color: white;
            border:none;
            border-radius: 10px;
            padding: 5px;
            font-size: 110%;
            &:hover{
                background: lighten($color: #8A2BE2, $amount: 40%);
                color: #8A2BE2;
                cursor: pointer;
            }
        }
        .proBtn{
            width:75%;
            background: darken($color: #8A2BE2, $amount: 30%);
            color: lighten($color: #8A2BE2, $amount: 90%);
            border:none;
            border-radius: 10px;
            padding: 5px;
            font-size: 110%;
            &:hover{
                background: darken($color: #8A2BE2, $amount: 40%);
                color: white;
                cursor: pointer;
            }
        }
    }
}